.container {
  position: relative;
  width: 100%;
  height: 100%;
  background: #fff;
  display: flex;
}

.container .leftside {
  padding: 10px;
  position: relative;

  flex: 17rem;
  background: #3a00d8;
  color: #fff;
}

.container .rightside {
  position: relative;
  flex: 70%;
  background: #e6e6fa;
}
.container .leftside .proimage {
  text-align: center;
}

.container .leftside .proimage img {
  height: 10rem;
  margin-top: 0.1rem;
  border-radius: 50%;
  border: solid rgb(188, 236, 243) 5px;
}
.container .leftside .contact {
  text-align: center;
  font-size: 1rem;
}
.container .leftside .contact .personal {
  font-size: 1.5rem;
  border-bottom: solid white 2px;
}
.container .leftside .contact .tit {
  text-align: left;
  font-weight: bold;
  color: #0afbff;
}
.container .leftside .contact .tites {
  margin-left: 0.2rem;
  text-align: left;
  font-size: 1rem;
  margin-top: -1rem;
  line-height: 1.6;
}

.rigthtit {
  margin-left: 0.5rem;
  font-weight: bold;
  color: rgb(43, 118, 247);
}

.rigthtites {
  text-indent: 50px;
  margin-left: 1.8rem;
  color: rgb(3, 75, 157);

  font-size: 1rem;
}
.rigthtitess {
  margin-right: 0.5rem;
  font-size: 0.5rem;
  margin-left: 0.5rem;
  color: rgb(3, 75, 157);
  font-size: 1rem;
}

table,
th,
td {
  border: 0.5px solid rgb(109, 164, 252);
  font-size: 10px;
  text-align: justify;
  color: rgb(3, 75, 157);
}

.Sch {
  font-size: 0.8rem;
  padding: 5px;
}
.yea {
  font-size: 0.8rem;
  text-align: center;
  line-height: 1.5;
  padding: 5px;
}
.Qua,
.sno {
  font-size: 0.8rem;
  padding: 5px;
  text-align: center;
}

.acd {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}
.sci {
  padding-left: 2rem;
  padding-right: 2rem;
  font-size: 0.8rem;
  text-align: center;
}
.cer {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 0.8rem;
}
.ins {
  padding-left: 1rem;
}
